#diamond {
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-bottom-color: #E9E9E9;
    position: relative;
    top: -10px;
}

#diamond:after {
    content: '';
    position: absolute;
    left: -10px;
    top: 10px;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-top-color: #E9E9E9;
}

